/* Special styles for printing */

#logo, .edit-button, .picu-brand, .picu-status-bar, .picu-select-item {
	display: none;
}

body, .picu-collection {
	width: 100%;
}

.picu-gallery-item {
	float: left !important;
	width: 20% !important;
	padding: 10px !important;
//	border: 1px solid black;

	&:nth-of-type(4n+1) {
		clear: both !important;
	}

	.picu-figure, .picu-imgbox, .picu-imgbox-inner, a, img {
		display: block !important;
		width: 100% !important;
		max-width: 100% !important;
		margin: 0 !important;
		padding: 0 !important;
	}
}

.picu-img-title {
	font-size: 7pt;
}